    /**
     * 
     */

    static offSetData() {
        let ratio = Display.sizeInPixels.height / Display.sizeInPixels.width;
        let deviceManufacturer = SdkManager.getInstance().getDeviceManufacturer();
        let androidSysVer = SdkManager.getInstance().getAndroidSysVer();
        let offset = -60; //60

        if (sys.os == sys.OS.ANDROID) {
            if (androidSysVer > 27) {
                if (SdkManager.getInstance().hasNotchInScreen()) {

                    // p4060
                    let safeAreaHeight = SdkManager.getInstance().getNotchHeight();
                    let r_scal = 1
                    if (Display.curResolutionPolicy == ResolutionPolicy.FIXED_WIDTH) {
                        r_scal = view.getVisibleSizeInPixel().width / Display.design_width
                    }
                    safeAreaHeight = -safeAreaHeight / r_scal;
                    if (safeAreaHeight < offset) {
                        offset = safeAreaHeight;
                    }
                    return offset
                }
            } else {
                if ((deviceManufacturer == "oppo" || deviceManufacturer == "OPPO") && SdkManager.getInstance().hasNotchAtOPPO()) {
                    return offset
                }
            }
        } else if (sys.os == sys.OS.IOS) {
            if (ratio > 2) {
                offset = offset - Display.getBottomNotchOffset() + 3;//+3
                return offset
            }
        }

        /** */
        // return -45;
        return 0
    }


