    /**
     * 
     * @param node 
     * @param ignoreChatBar 
     * @param hasOffsetChatBarHeight 
     * @param isNormalWindow ignoreChatBarhasOffsetChatBarHeightnormal
     */
    static alignBottom(node?: Node, ignoreChatBar?: boolean, hasOffsetChatBarHeight?: boolean, isNormalWindow?: boolean): any {
        //
        // let fit_x = 0
        let fit_y = 0
        // ipx
        // if (Display.isShowAll()) {
        //     return fit_y;
        // }
        // let r_scal = 1
        if (Display.curResolutionPolicy == ResolutionPolicy.FIXED_WIDTH) {
            if (isNormalWindow) {
                //ui
                let eZone = Display.height - Display.getBottomOffset();
                //uiuiui
                fit_y = -(eZone - Display.getUIZoneHeight()) / 2;
                if (node) {
                    node.setPosition(node.getPosition().x, node.getPosition().y + fit_y);
                }
                return fit_y;
            }

            let r_scal = Display.sizeInPixels.width / Display.design_width;
            fit_y = -(Display.sizeInPixels.height / r_scal - Display.design_height) / 2;

        }

        if (node) {
            if (ignoreChatBar) {
                // node.y = (node.y + fit_y) + Display.getBottomNotchOffset();
                fit_y += Display.getBottomNotchOffset();
            } else {
                // node.y = (node.y + fit_y) + Display.getBottomNotchOffset() + Display.getBottomChatHeight();
                fit_y += Display.getBottomOffset();
                //prefab
                if (hasOffsetChatBarHeight) {
                    fit_y -= Display.chatBarMinHeight;
                }
            }

            node.setPosition(node.getPosition().x, node.getPosition().y + fit_y);
        }

        return fit_y;
    }


    //iphoneX
    static getBottomNotchOffset() {
        let offset = 0;
        if (sys.os == sys.OS.IOS) {
            let ratio = Display.sizeInPixels.height / Display.sizeInPixels.width;
            offset = ratio > 2 ? Display.iphonex_bottom_height : offset;
        }
        return offset;
    }



    //
    static getBottomChatHeight() {
        if (Display.bottomChatHeight === undefined) {
            let minHeight = Display.chatBarMinHeight;//
            let maxHeight = Display.chatBarMaxHeight;//
            let disY = 0;
            if (Display.curResolutionPolicy == ResolutionPolicy.FIXED_WIDTH) {
                let r_scal = Display.sizeInPixels.width / Display.design_width;
                disY = Display.sizeInPixels.height / r_scal - Display.design_height;
            }
            //
            let height = Math.min(Math.max(disY, minHeight), maxHeight);
            Display.bottomChatHeight = height;
        }

        return Display.bottomChatHeight;

    }

