    // 
    static getMaxScal(): Vec3 {
        if (Display.isShowAll()) {
            return v3(1, 1, 1);
        }
        if (Display.curResolutionPolicy == ResolutionPolicy.FIXED_WIDTH) {
            let frameSize = Display.sizeInPixels
            let ratio_width = frameSize.width / Display.design_width
            let ratio_height = frameSize.height / Display.design_height
            let scale = 1
            if (ratio_width > ratio_height) {
                scale = Number(1 / ratio_height * ratio_width)
            } else {
                scale = Number(1 / ratio_width * ratio_height)
            }
            return v3(scale, scale, scale);
        }
        return v3(1, 1, 1);
    }

