    /**
     * label
     * @param fontSize 
     * @param param  {verticalAlign: useFnt:fnt}
     */
    static newLabel(fontSize?: number, param?: any): Node {

        let node = new Node();
        // node.addComponent(UITransform);
        node.addComponent(UIOpacity);
        param = param || {}
        let label = node.addComponent(Label);
        if (param.useFnt) {
            label.font = TextureManager.getDefaultBMPFont();
            label.spacingX = 5;
        } else {
            label.font = TextureManager.getTTFont();
        }
        // +
        label.horizontalAlign = Label.HorizontalAlign.CENTER
        if (param && param.verticalAlign) {
            label.verticalAlign = param.verticalAlign;
        } else {
            label.verticalAlign = Label.VerticalAlign.CENTER
        }
        if (fontSize) {
            if (param.useFnt) {
                label.lineHeight = fontSize + 6; //
            } else {
                label.fontSize = fontSize
                label.lineHeight = fontSize + 4; //
            }

        }
        return node;
    }

