    static new3dSprite(name?: string): Node {
        let node: Node = new Node(name);
        let sprite = node.addComponent(Sprite);
        // node.addComponent(UITransform);
        node.addComponent(UIOpacity);
        sprite.type = Sprite.Type.SLICED;
        node.layer = GroupLayer.SCENE3D;
        let curMater = sprite.getMaterial(0);
        if (curMater)
            curMater.destroy();
        curMater = TextureManager.getMaterialByUrl(CusMaterial.SPRITE);
        sprite.setMaterial(curMater, 0);
        return node;
    }

    static timerMoveAction(widget: any, endPoint: Vec2, token: string, speed: number, callback: Function, callbackData: any) {
        if (widget["moveTimeUpdate"]) {
            TimerManager.removeTimer(widget["moveTimeUpdate"])
        }
        if (widget["token"]) {
            TimerManager.removeTimerWithToken(widget["token"])
        }

        speed = speed || 20
        widget["moveTimeToken"] = token
        widget["moveTimeSpeed"] = speed
        widget["moveTimeEndPoint"] = endPoint
        widget["moveTimeCallBack"] = callback
        widget["moveTimeCallbackData"] = callbackData

        widget["moveTimeUpdate"] = function (sender: any) {
            let beginPoint = v2(sender.getPosition())
            let calcLinear = Utils.calcLinearVelocity(beginPoint, sender.moveTimeEndPoint, sender.moveTimeSpeed)
            let movePos = v2(beginPoint.x + calcLinear.x, beginPoint.y + calcLinear.y)
            sender.setPosition(movePos)
            beginPoint = movePos
            if (Utils.compareDistance(beginPoint, sender.moveTimeEndPoint, sender.moveTimeSpeed) <= 0) {
                log("close token", sender.moveTimeToken)
                TimerManager.removeTimer(widget["moveTimeUpdate"])
                TimerManager.removeTimerWithToken(sender.moveTimeToken)
                sender.setPosition(sender.moveTimeEndPoint)
                if (sender.moveTimeCallBack) {
                    if (sender.moveTimeCallbackData) {
                        sender.moveTimeCallBack(sender.moveTimeCallbackData)
                    } else {
                        sender.moveTimeCallBack()
                    }
                }
            }
        }
        TimerManager.addFrame(1, widget["moveTimeUpdate"], null, true, widget, widget["moveTimeToken"])
    }

