    static filterGrey(node: Node, isGrey: boolean): void {
        if (!node) {
            return;
        }

        let btnComp = node.getComponent(Button);
        if (btnComp) {
            btnComp.disabledSprite = null
        }

        let clickComp = node.getComponent(ComButton);
        if (clickComp) {
            clickComp.disabledSprite = null
        }

        let spComp: Sprite = node.getComponent(Sprite);
        if (spComp) {
            spComp.grayscale = isGrey;
        }

        let labelComp: Label = node.getComponent(Label);
        if (labelComp) {
            if (isGrey) {
                const mat = builtinResMgr.get<Material>('ui-sprite-gray-material')
                labelComp.customMaterial = mat
            } else {
                labelComp.customMaterial = undefined // material 
            }
        }

        for (let i = 0; i < node.children.length; i++) {
            let child = node.children[i];
            Display.filterGrey(child, isGrey);
        }
    }


