    /**
     * 
     * (Display.unloadImage)
     * @param node Sprite
     * @param path  
     * @param tCallback null
     * @param targetObj null
     * @param isAutoReleaseImage true,false:
     * @param lev  0
     */
    static loadAndSetImage(obj: Node | Sprite, path: string, tCallback: Function = null, targetObj: any = null, isAutoReleaseImage: boolean = true, lev: number = 0, bundleName = BundleName.Resources): boolean {
        if (!obj) {
            return false;
        }
        let node;
        if (obj instanceof Sprite) {
            node = obj.node;
        } else {
            node = obj;
        }
        let comp: LoadAndSetImageComponent = node.getComponent(LoadAndSetImageComponent);
        if (!comp) {
            comp = node.addComponent(LoadAndSetImageComponent);
        }

        comp.setResPath(path, tCallback, targetObj, isAutoReleaseImage, lev, bundleName);

        return true;
    }

