    /**
     * 
     * @param target 
     * @param pos 
     * @param radiusA 
     * @param radiusB 
     * @param speed 
     * @param isCounterclockwise 
     * @param token 
     * @param angle 
     * @param isCheckZ 
     * @param loops
     * @param cbData
     */
    static playEllipseAction(target: Node, pos, radiusA, radiusB, speed: number, isCounterclockwise, token, angle: number, isCheckZ, loops: number, cbData?: CallbackData) {
        TimerManager.removeTimerWithToken(token);

        angle = angle ? angle : 0;
        let time: number = 10;
        speed = speed * time / 1000;

        // 
        loops = loops ? loops : 0;
        let baseAngle: number = 0;
        let count: number = 0;

        TimerManager.addTimer(10, function () {
            if (isCounterclockwise == true) {
                angle = angle + speed;
                if (angle >= 360) {
                    angle = 0;
                }
            } else {
                angle = angle - speed;
                if (angle <= 0) {
                    angle = 360;
                }
            }
            if (isCheckZ) {
                if (angle > 40 && angle < 140) {
                    target.setPosition(v3(target.getPosition().x, target.getPosition().y, 0));
                } else {
                    target.setPosition(v3(target.getPosition().x, target.getPosition().y, 1));
                }
            }

            if (loops > 0) {
                baseAngle = baseAngle + speed;
                if (baseAngle >= 360) {
                    baseAngle = baseAngle - 360;
                    count = count + 1;
                    if (count >= loops) {
                        TimerManager.removeTimerWithToken(token);
                        if (cbData) {
                            cbData.execute();
                        }
                    }
                }
            }

            let x = radiusA * Math.cos(Display.angle2radian(angle)) + pos.x;
            let y = radiusB * Math.sin(Display.angle2radian(angle)) + pos.y;
            target.setPosition(x, y);
        }, target, true, null, token);
    }

    /**
     * 
     * @param angle 
     */
    static angle2radian(angle: number): number {
        return angle * (Math.PI / 180);
    }



