    public static scaleForeverAnim(target: Node, bigS?: number, smallS?: number, time?: number) {
        Tween.stopAllByTarget(target);
        let toScale1: number | Vec3 = bigS || 0.1;
        toScale1 = v3(target.scale.x + toScale1, target.scale.y + toScale1, target.scale.z + toScale1);
        let toScale2: number | Vec3 = -(smallS || 0.1);
        toScale2 = v3(target.scale.x + toScale2, target.scale.y + toScale2, target.scale.z + toScale2);

        time = time || 0.6
        tween(target).repeatForever(tween(target).to(time, { scale: toScale1.clone() }).to(time, { scale: toScale2.clone() })).start();
    }

