    static doShake(node: Node) {
        let pos = node.getPosition();
        let offset = 10;
        tween(node).to(0.03, {
            position: v3(pos.x - (2 + offset), pos.y + (offset + 10))
        }).to(0.03, {
            position: v3(pos.x + (1 + offset), pos.y - (offset + 1))
        }).set({
            position: pos
        }).union().repeat(2).start();
    }

    static showNum(num: number) {
        let text = ""
        if (num >= 10000 && num < 100000000) {
            let thousand = Math.floor(num / 10000)
            let kilobit = Math.floor(num % 10000 / 1000)//
            let hundred = Math.floor(num % 1000 / 100)//
            return text = thousand + "." + kilobit + language.numlv.wan
        } else if (num >= 100000000) {
            let hundreds_millions = Math.floor(num / 100000000)
            let millions_millions = Math.floor(num % 100000000 / 10000000) //
            let million = Math.floor(num % 10000000 / 1000000) //
            return text = hundreds_millions + "." + millions_millions + language.numlv.yi
        } else {
            return text = num + ""
        }
    }



