    /**
     * 
     * @params 
     */
    static moveOutHidWithNode(params: Node | Node[]) {
        if (params instanceof Node) {
            let moisCom = params.getComponent(MoveOutInScreenComponent)
            if (!moisCom) {
                moisCom = params.addComponent(MoveOutInScreenComponent)
            }
            moisCom.moveOut();
            params.active = false;
        } else {
            for (const i in params) {
                const node = params[i];
                let moisCom = node.getComponent(MoveOutInScreenComponent)
                if (!moisCom) {
                    moisCom = node.addComponent(MoveOutInScreenComponent)
                }
                moisCom.moveOut();
                node.active = false;
            }
        }

    }


    /**
     * 
     * @params 
     * @originalX 
     * @noDelay 
     */
    static moveInShowWithNode(params: Node | Node[], originalX?: number, noDelay?: boolean) {
        if (!params) {
            return;
        }
        if (params instanceof Node) {

            let moisCom = params.getComponent(MoveOutInScreenComponent)
            if (!moisCom) {
                moisCom = params.addComponent(MoveOutInScreenComponent)
            }
            moisCom.moveIn(noDelay, originalX);
            params.active = true;
        } else {
            for (const i in params) {
                const node = params[i];
                let moisCom = node.getComponent(MoveOutInScreenComponent)
                if (!moisCom) {
                    moisCom = node.addComponent(MoveOutInScreenComponent)
                }
                moisCom.moveIn(noDelay, originalX);
                node.active = true;
            }
        }
    }

